.tag {
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 3px 10px;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
  z-index: 10;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}
.tag-new {
  background-color: #dbeafe;
  color: #1e40af;
}
.tag-out-of-stock {
  background-color: #fee2e2;
  color: #991b1b;
}
.tag-coming-soon {
  background-color: #f3e8ff;
  color: #6b21a8;
}
.tag-sale {
  background-color: #dcfce7;
  color: #166534;
}
