  transition: all 0.3s ease;
}
.group-hover .absolute {
  animation: fadeInDown 0.2s ease-out forwards;
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.mobile-categories {
  transition: all 0.3s ease;
  max-height: 0;
  overflow: hidden;
}
.mobile-categories:not(.hidden) {
  max-height: 500px;
}
.cart-qty-btn {
  width: 24px;
  height: 24px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e2e8f0;
  border-radius: 4px;
  background: #f7fafc;
  cursor: pointer;
}
.cart-qty-btn:hover {
  background: #edf2f7;
}
#contact-response, 
#newsletter-response {
  transition: all 0.3s ease;
}
.product-card {
  transition: all 0.3s ease;
}
.product-card:hover {
  transform: translateY(-5px);
}
.group:hover .group-hover\:scale-110 {
  transform: scale(1.1);
}
button, 
a[class*="bg-"], 
a[class*="border"] {
  transition: all 0.3s ease;
}
@keyframes pulse {
  0%, 100% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.8;
  }
}
.animate-pulse {
  animation: pulse 1.5s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}
#mobile-menu {
  transition: max-height 0.3s ease;
  max-height: 0;
  overflow: hidden;
}
#mobile-menu:not(.hidden) {
